  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Skip link for accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #333333;
  color: #fff;
  padding: 8px;
  z-index: 100;
  text-decoration: none;
}

/* Skip link will appear when focused */
.skip-link:focus {
  top: 0;
}

/* App Bar */
.app-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FF6347;
  color: white;
  padding: 1rem 2rem;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 100;
}

/* Brand styling with icon */
.brand {
  display: flex;
  align-items: center;
}

.brand i {
  color: #fff;
  margin-right: 10px;
}

.brand h1 {
  font-size: 1.8rem;
  color: white;
}

/* Navigation links for desktop */
.nav-links {
  display: flex;
  list-style: none;
  padding-left: 0;
  gap: 1.5rem;
}

.nav-links li {
  display: inline;
}

.nav-links a {
  display: inline-block;
  align-items: center;
  color: white;
  text-decoration: none;
  font-size: 1.1rem;
  padding: 10px 15px;
  min-width: 44px;
  min-height: 44px;
  transition: color 0.3s ease;
}

.nav-links a i {
  margin-right: 5px;
}

.nav-links a:hover {
  color: #FAEBD7;
}

/* Hamburger menu */
#menu-toggle {
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  display: none;
  min-width: 44px;
  min-height: 44px;
}

/* Mobile Menu Hidden by Default */
@media (max-width: 768px) {
  .nav-links {
    display: none;
    flex-direction: column;
    background-color: #FF6347;
    position: fixed;
    top: 60px;
    left: 0;
    width: 100%;
    text-align: center;
    justify-content: center;
    padding-top: 10px;
    z-index: 99;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    opacity: 0;
    transform: translateY(-10px);
    transition: all 0.4s ease-in-out;
  }

  .nav-links a {
    color: #fff;
    font-size: 1.5rem;
    padding: 10px 15px;
    margin: 0.5rem;
    border-radius: 5px;
    min-width: 44px;
    min-height: 44px;
    transition: all 0.3s ease-in-out;
    text-align: center;
  }

  /* Add overlay behind mobile menu for focus */
  .menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 98;
    display: none;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  /* Show mobile menu and overlay */
  #menu-toggle[aria-expanded="true"] + .nav-links {
    display: flex;
    opacity: 1;
    transform: translateY(0);
  }

  #menu-toggle[aria-expanded="true"] + .nav-links + .menu-overlay {
    display: block;
    opacity: 1;
  }

  #menu-toggle {
    display: block;
  }
}

/* Reset menu display when resizing back to desktop */
@media (min-width: 769px) {
  .nav-links {
    display: flex !important;
    position: static;
    flex-direction: row;
    width: auto;
    background: none;
    box-shadow: none;
    opacity: 1;
    transform: none;
  }

  #menu-toggle {
    display: none;
  }

  .menu-overlay {
    display: none;
  }
}

