.hero {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  position: relative;
  width: 100%;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.hero-content {
  z-index: 2;
}

.hero-content h2 {
  font-size: 2.5rem;
  margin: 0;
}

.hero-content p {
  margin: 0.5rem 0 0;
  font-size: 1.2rem;
}

/* Main content and background */
body {
  background-color: #FAEBD7;
  font-family: Arial, sans-serif;
}

/* Center and style for Restaurant List title */
.section-title {
  text-align: center;
  padding: 2rem 0;
  font-size: 2rem;
  color: #333333;
}

#restaurant-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  padding: 2rem;
}

@media (max-width: 320px) {
  #restaurant-list {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
  }
}

/* Effect for restaurant item hover */
.restaurant-item {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
  position: relative;
}

.restaurant-item:hover {
  transform: scale(1.05);
}

/* Rating Star Overlay */
.restaurant-item .rating {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #FFD700;
  font-size: 1.2rem;
  padding: 5px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.rating i {
  margin-right: 5px;
}

.restaurant-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.restaurant-info {
  padding: 1rem;
}

.restaurant-info h3 {
  margin: 0;
  font-size: 1.5rem;
  color: #333333;
}

.restaurant-info p {
  margin: 0.5rem 0;
  color: #333333;
}

/* Footer */
footer {
  text-align: center;
  padding: 1rem;
  background-color: #FF6347;
  color: white;
  font-size: 1rem;
  width: 100%;
}

/* Responsive styles */
@media (min-width: 768px) {
  .nav-links {
    display: flex;
    gap: 1rem;
  }
}

@media (min-width: 1200px) {
  .hero {
    height: 500px;
    background-size: cover;
  }

  .hero-content h2 {
    font-size: 3.5rem;
  }

  .hero-content p {
    font-size: 1.5rem;
  }
}
/* Kontainer untuk Detail Restoran */
#restaurant-detail {
  display: none;
  justify-content: center; /* Pusatkan card detail */
  align-items: center;
  padding: 2rem;
  width: 100%;
}

/* Card Detail Restoran */
/* Card Detail Restoran */
.restaurant-detail-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 2rem;
  max-width: 600px;
  width: 100%;
  text-align: left; /* Pastikan konten sejajar ke kiri */
}

.restaurant-detail-card h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: #333333;
  text-align: center; /* Hanya judul yang di center */
}

.restaurant-detail-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin: 1rem 0;
}

.restaurant-detail-card p,
.restaurant-detail-card ul,
.restaurant-detail-card li {
  font-size: 1rem;
  margin: 0.5rem 0;
  color: #333333;
}

.restaurant-detail-card ul {
  padding-left: 1.5rem;
  list-style-type: disc;
}

.favorite-button {
  margin-top: 1.5rem;
  padding: 0.5rem 1.5rem;
  background-color: #FFD700;
  color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.favorite-button:hover {
  background-color: #e6c200;
}

.back-button {
  margin-top: 1.5rem;
  padding: 0.5rem 1.5rem;
  background-color: #FF6347;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #e6533d;
}

/* Styling untuk Tombol View Details */
.view-details-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #FF6347;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.view-details-button:hover {
  background-color: #e6533d;
}
.favorite-button {
  margin-top: 1.5rem;
  padding: 0.5rem 1.5rem;
  background-color: #FFD700;
  color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.favorite-button:hover {
  background-color: #e6c200;
}


/* Styling khusus untuk Favorite List */
#favorite-restaurant-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
  padding: 2rem;
}

#favorite-restaurant-list .restaurant-item {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: calc(33.333% - 1rem); /* Lebar 1/3 dari kontainer, dengan gap antar item */
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
}

#favorite-restaurant-list .restaurant-item:hover {
  transform: scale(1.05);
}
